import axios from '@/libs/api.request'
import baseAxios from '../libs/base.request'

/**
 * pad后端-查询管理员列表
 * @param params
 * @returns {*}
 */
export const getUserListByCondition = params => {
  return axios.request({
    url: '/padMgmt/getUserListByCondition',
    data: params,
    method: 'post'
  })
}
/**
 * pad后端-删除管理员
 * @param params
 * @returns {*}
 */
export const deleteUser = params => {
  return axios.request({
    url: '/padMgmt/deleteUser',
    params,
    method: 'get'
  })
}

