<template>
  <Layout class="account-mgt gary">
    <Form inline :label-width="80">
      <FormItem label="姓名">
        <Input v-model="formData.name" placeholder="请输入真实姓名"></Input>
      </FormItem>
      <FormItem label="电话号码">
        <Input v-model="formData.phone" placeholder="请输入电话号码"></Input>
      </FormItem>
      <FormItem label="员工编号">
        <Input v-model="formData.empNo" placeholder="请输入员工编号"></Input>
      </FormItem>
      <FormItem  label="地区">
        <Select v-model="formData.areaCode" filterable clearable>
          <Option
            v-for="item in cityList"
            :value="item.cityCode"
            :key="item.id"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <Button type="primary" @click="search">查询</Button>
      <Button type="primary" @click="add">新增</Button>
    </Form>
    <Table :columns="cols" :data="dataList" style="margin-bottom: 10px">
      <template slot-scope="{ row }" slot="opt">
        <Button type="primary" @click="edit(row)">修改</Button>
        <Button type="error" @click="delUser(row.id)">删除</Button>
      </template>
    </Table>
    <div style="text-align: right">
      <Page
        :total="totalCount"
        :page-size="formData.pageSize"
        :current="formData.pageNo"
        show-elevator
        show-sizer
        show-total
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"></Page>
    </div>
    <Modal
      :mask-closable="false"
      v-model="editModal"
      width="400"
      title="账号编辑">
      <Form :label-width="80" ref="formValidate" :model="row" :rules="rules">
        <FormItem label="姓名" prop="name">
          <Input v-model="row.name" style="width: 200px" placeholder="请输入真实姓名"></Input>
        </FormItem>
        <FormItem label="电话号码" prop="phone">
          <Input v-model="row.phone" style="width: 200px" placeholder="请输入电话号码"></Input>
        </FormItem>
        <FormItem label="员工编号" prop="empNo">
          <Input v-model="row.empNo" style="width: 200px" placeholder="请输入员工编号"></Input>
        </FormItem>
        <FormItem  label="地区" prop="cityCode">
          <Select v-model="row.cityCode" style="width: 200px" filterable clearable>
            <Option
              v-for="item in cityList"
              :value="item.cityCode"
              :key="item.id"
            >{{ item.name }}</Option>
          </Select>
<!--          <RadioGroup v-model="row.cityCode">-->
<!--            <Radio style="width: 100px" v-for="item in cityList"-->
<!--                   :key="item.id"-->
<!--                   :label="item.cityCode">-->
<!--              {{ item.name }}-->
<!--            </Radio>-->
<!--          </RadioGroup>-->
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="save">保存</Button>
        <Button @click="editModal = false">取消</Button>
      </div>
    </Modal>
  </Layout>
</template>

<script>
import {
  getUserListByCondition,
  getAllCity,
  deleteUser,
  addUser,
  updateUser
} from '@/api/mallMgt'
export default {
  name: 'accountMgt',
  data () {
    return {
      formData: {
        areaCode: '',
        name: '',
        phone: '',
        empNo: ''
        // pageSize: 10,
        // pageNo: 1
      },
      totalCount: 0,
      cols: [
        {
          title: '序号',
          type: 'index',
          align: 'center',
          width: 100
        },
        {
          title: '真实姓名',
          key: 'name',
          align: 'center'
        },
        {
          title: '手机号码',
          key: 'phone',
          align: 'center'
        },
        {
          title: '员工编号',
          key: 'empNo',
          align: 'center'
        },
        {
          title: '地区',
          key: 'areaName',
          align: 'center'
        },
        // {
        //   title: '权限',
        //   key: 'remark',
        //   align: 'center'
        // },
        {
          title: '操作',
          slot: 'opt',
          align: 'center'
        }
      ],
      dataList: [],
      cityList: [],
      editModal: false,
      row: {
        cityCode: '',
        name: '',
        phone: '',
        empNo: ''
      },
      rules: {
        cityCode: [
          { required: true, message: '地区不能为空', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '姓名不能为空', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '电话号码不能为空', trigger: 'blur' }
        ],
        empNo: [
          { required: true, message: '员工编号不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    getTableData () {
      getUserListByCondition(this.formData).then(data => {
        this.dataList = data.records
        this.totalCount = data.totalCount
      }).catch(err => {
        this.$Message.error({
          content: '查询列表数据失败：' + err.message,
          duration: 5
        })
      })
    },
    getCityList () {
      getAllCity().then(data => {
        this.cityList = data
      }).catch(err => {
        this.$Message.error({
          content: '查询城市列表失败：' + err.message,
          duration: 5
        })
      })
    },
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    // 分页
    changePage (num) {
      this.formData.pageNo = num
      this.getTableData()
    },
    search () {
      this.formData.pageNo = 1
      this.getTableData()
    },
    delUser (id) {
      this.$Modal.confirm({
        title: '删除确认',
        content: '您确定删除此条记录吗？',
        onOk: () => {
          deleteUser({
            id
          }).then(() => {
            this.$Message.success({
              content: '删除成功',
              duration: 5
            })
            this.search()
          }).catch(err => {
            this.$Message.error({
              content: '删除失败：' + err.message,
              duration: 5
            })
          })
        }
      })
    },
    add () {
      this.$refs.formValidate.resetFields()
      this.row = {
        areaCode: '',
        name: '',
        phone: '',
        empNo: ''
      }
      this.editModal = true
    },
    edit (row) {
      this.$refs.formValidate.resetFields()
      this.row = row
      this.editModal = true
    },
    save () {
      this.$refs.formValidate.validate(valid => {
        if (valid) {
          this.row.areaName = this.cityList.find(r => r.cityCode === this.row.cityCode).name
          if (this.row.id) {
            updateUser(this.row).then(() => {
              this.$Message.success({
                content: '修改成功',
                duration: 5
              })
              this.search()
              this.editModal = false
            }).catch(err => {
              this.$Message.error({
                content: '修改失败：' + err.message,
                duration: 5
              })
            })
          } else {
            addUser(this.row).then(() => {
              this.$Message.success({
                content: '新增成功',
                duration: 5
              })
              this.search()
              this.editModal = false
            }).catch(err => {
              this.$Message.error({
                content: '新增失败：' + err.message,
                duration: 5
              })
            })
          }
        }
      })
    }
  },
  mounted () {
    this.getCityList()
    this.search()
  }
}
</script>
<style scoped lang="less">
  .account-mgt {
    padding: 10px;
    .wrapper {
      width: 100%;
      background: white;
      padding: 10px;
    }
  }
</style>
